.nav-comp {
    display: flex;
    background:transparent;
    justify-content: space-between;
    align-items: center;
    /* padding-left: 3vw; */
    /* padding-right: 4vw; */
    height: 11vh;
    position: relative;
    width: 100vw;
    z-index: 20;
    user-select: none;
}

.event-nav-left {
    width: 90px;
    padding-left: 3vh;
}

.event-nav-right {
    display: flex;
    gap: 3vw;
    height: 100%;
    align-items: center;
    padding-right: 4vh;
}

@media only screen and (max-width:980px) and (min-width:768px){
    .event-nav-right-compo{
        font-size: 15px !important;
    }
}

.event-nav-right-compo {
    color: var(--default-2, #FFF);
    text-align: justify;
    leading-trim: both;
    text-edge: cap;
    font-family: Salsa;
    font-size: 20px;
    font-style: normal;
    cursor: default;
    font-weight: 700;
    line-height: normal;
    text-decoration: none;
    padding: 8px;
}

.event-nav-right-compo:hover {
    cursor: pointer;
    /* text-decoration: underline; */
}



