.inner_mob_nav {
    display: flex;
    width: 100%;
    position: relative;
    align-items: center;
    z-index: 100;
    justify-content: space-between;
    padding: 0 10px;
}

@keyframes slide {
    0% {
        right: -100vw;
    }

    100% {
        right: 0vw;
        background-color: black;
        background-size: cover;
        background-position: center center;
    }
}

@keyframes slide1 {
    0% {
        right: 0vw;
        background-color: black;
    }

    100% {
        right: -100vw;
        background-color: black;
        background-size: cover;
        background-position: center center;
    }
}

.slidebar1 {
    position: fixed;
    top: 0vh;
    right: 0vw;
    width: 100vw;
    z-index: 20;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: slide .6s forwards;
    padding-bottom: 20px;
    min-height: 108vh;
}

.slidebar2 {
    position: fixed;
    top: 0vh;
    right: -100vw;
    width: 100vw;
    z-index: 20;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: slide1 .6s;
    padding-bottom: 20px;
    min-height: 108vh;
}

.slidebar1 h5 {
    color: #FFF;
    text-align: justify;
    leading-trim: both;
